<script setup lang="ts">
import { computed, ref, watch } from "vue";
import {
  ARow,
  ACol,
  ASelect,
  AContainer,
  ATextField,
  ActindoDataList,
  type ICellProperty,
} from "@actindo/core-ui";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesStringValueBadWords } from "./models";
import { BAD_WORDS_LIST } from "@/components/dataTypes/StringValue/constants";

const props = withDefaults(
  defineProps<{
    scopable: boolean;
    remoteScopes?: ITenantAttributeScope[];
    multiLanguage: boolean;
    remoteLanguages?: ITenantAttributeLanguage[];
  }>(),
  {
    remoteScopes: () => [],
    remoteLanguages: () => [],
  },
);

const badwords = defineModel<IDataTypesStringValueBadWords>({
  default: () => [],
});

const currentScopeId = ref();
const currentLanguageId = ref();

const hideBadWords = computed(
  () =>
    (props.multiLanguage && !currentLanguageId.value) ||
    (props.scopable && !currentScopeId.value),
);

const activeBadWords = computed(
  () =>
    badwords.value?.[currentScopeId.value]?.[currentLanguageId.value] ?? [""],
);

watch(
  [currentScopeId, currentLanguageId],
  ([currentScope = 0, currentLanguage = 0]) => {
    if (!badwords.value) {
      badwords.value = [];
    }
    if (!badwords.value[currentScope]) {
      badwords.value[currentScope] = {};
    }
    if (!badwords.value[currentScope][currentLanguage]) {
      badwords.value[currentScope][currentLanguage] = [""];
    }
  },
  { immediate: true },
);

function deleteWord({ rowIndex }: ICellProperty) {
  if (
    badwords.value[currentScopeId.value][currentLanguageId.value].length > 1
  ) {
    badwords.value[currentScopeId.value][currentLanguageId.value].splice(
      rowIndex,
      1,
    );
  } else {
    badwords.value[currentScopeId.value][currentLanguageId.value] = [""];
  }
}

function addWord() {
  badwords.value[currentScopeId.value][currentLanguageId.value].push("");
}
</script>

<template>
  <AContainer>
    <ARow>
      <ACol cols="12" class="d-flex align-center">
        <strong class="mr-2">{{ $t("stringValue.badWords") }}</strong>
        <ASelect
          v-if="scopable && remoteScopes.length"
          v-model="currentScopeId"
          :items="remoteScopes"
          :label="$t('stringValue.scope')"
          item-title="name"
          item-value="id"
          class="mr-2"
        />
        <ASelect
          v-if="multiLanguage && remoteLanguages.length"
          v-model="currentLanguageId"
          :items="remoteLanguages"
          :label="$t('stringValue.language')"
          item-title="description"
          item-value="id"
          class="mr-2"
        />
      </ACol>
      <ACol>
        <template v-if="hideBadWords">
          {{ $t("stringValue.wrongScopeLanguage") }}
        </template>
        <template v-else>
          <ActindoDataList
            :data="activeBadWords"
            :create-actions="[
              { title: $t('stringValue.addWord'), icon: 'add_circle' },
            ]"
            v-bind="BAD_WORDS_LIST"
            @create="addWord"
            @remove="deleteWord"
          >
            <template #cell-text="{ rowIndex }">
              <ATextField
                v-if="!hideBadWords"
                v-model="badwords[currentScopeId][currentLanguageId][rowIndex]"
              />
            </template>
          </ActindoDataList>
        </template>
      </ACol>
    </ARow>
  </AContainer>
</template>
